@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Muli', sans-serif;

}
.navbar{
  background-color: #fff !important;
}
.navbar-brand{
  margin-top: 10px;
  font-size: 1.8rem !important;
   letter-spacing: 3px;
}
.navbar a{
  font-size: 1.2rem;
  text-transform: capitalize;
}
.menu_active{
  font-weight: bold;
  border-bottom:1px solid #ff8000;
} 
.navbar a:hover{
opacity:0.5;
}

@media (max-width:991px){
  .navbar-brand{
    font-size: 1.5rem;
  }
  .menu_active{
    border-bottom: none;
  }
}

.header-content
{
  font-size:2.7rem;

}

.footer-one{
  position: relative;
  top:120px;
}
.footer-para{
  
  margin-top: 10px;
}


